.main{
  padding: 20px 0 20px 0;
  width: 100%;
  height: 100%;
  background-color: rgb(147, 128, 128);
  border: 1px solid black;
}
 
.wrap_main{
  width: 80%;
  height: 100%;
  margin: 0 auto;
  padding: 10px 10px 10px 30px;
  background-color: rgb(255, 255, 255);
  border: 1px solid black;
}
.title,
h1{
  font-size: 50px;
  font-weight: bold;
  text-align: center;
  background-image: linear-gradient(to left, hsl(243, 77%, 41%), #a00351);
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  margin-bottom: 20px; 
}
img{
  width: 350px;
  height: 600px;
}
.wrap_info{
  display: flex;
  justify-content: auto;
  text-indent: 30px;
  text-align: justify;
}
.info{
  margin-left: 30px;
  font-size: 20px;
  font-weight: bold;
  padding-right: 20px;
}
.card_info{
  width: 50%;
  margin: 20px 20px 20px 20px;
  border: 3px solid black;
  border-radius: 20px;
  box-shadow: 10px 10px 10px 10px rgb(86, 86, 86);
}
.imgAuthor{
  width: 40%;
  margin:5% 0 0 5%;
  height: 200px;
  object-fit: cover;
  border: 3px solid rgb(78, 36, 136);
  border-radius: 10px;
}
.places_work{
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap:10px;
  padding: 0 15px 35PX 15px;
  margin-bottom: 150px;
}
.places_card{
  position: relative;
  overflow: hidden;
}
.places_card::after{
  display: block;
  content:'';
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}
.places_card:hover .places_card_img{
  transform: scale(1.2);
}
.places_size{
  grid-column: span 3;
}
.places_card_img{
  width: 100%;
  transition: 0.3s all;
  object-fit: fill;
}
.places_card_img-lg{
  height: 550px;
  object-fit: fill;
}
.places_card_img-sm{
  height: 400px;
  object-fit: cover;
}
.places_size-sm{
  grid-column: span 2;
}
.places_card_title{
  font-weight: 700;
  font-size: 30px;
  line-height: 60px;
  color:#fff;
  position: absolute;
  bottom: 27px;
  left: 29px;
  z-index: 3;
}